.tarjeta {
    margin-top: 20px;
    text-align: center;
    background: rgb(240, 240, 240);
}

.tarjeta figure {
    position: relative;
    overflow: hidden;
}

.tarjeta figure img {
    width: 100%;
    transform: scale(1.1);
    filter: saturate(1.2) contrast(1.1);
}

.tarjeta figure .verificada, .no_verificada {
    position: absolute;
    z-index: 1;
    bottom: 5px;
    left: 5px;
    padding: 2px 6px;
    border-radius: 5px;
    font-size: 0.9rem;
    font-weight: 500;
    text-transform: uppercase;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.6);
    cursor: pointer;
}

.verificada {
    background: rgb(33, 218, 58);
}

.no_verificada {
    background: rgb(228, 57, 45);
}

.etiqueta {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(-45deg);
    color: rgba(0, 0, 0, 0.7);
    font-family: 'Racing Sans One', cursive;
    font-size: 2.2rem;
}

.datos {
    padding: 0 0 10px 0;
}

.nacionalidad {
    position: absolute;
    z-index: 1;
    bottom: 35px;
    left: 5px;
    padding: 2px 6px;
    border-radius: 5px;
    font-size: 0.9rem;
    background: rgb(238, 234, 21);
    font-weight: 500;
    text-transform: uppercase;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.6);
    cursor: pointer;
}

.distritos {
    padding: 0 10px;
    font-size: 0.8rem;

    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 5px 10px;
}

.distritos p {
    padding: 2px 6px;
    border-radius: 5px;
    background: violet;
}

.informacion {
    padding: 0 10px;
    display: block;
    line-height: 1.2em;
    overflow: hidden;
    height: 8.5em;
    text-overflow: ellipsis;
    word-wrap: break-word;
}