.espacio {
    width: 100%;
    height: 60px;
}

.menu {
    position: fixed;
    z-index: 2;
    width: 100%;
    height: 60px;
    background: rgb(22, 7, 58);
    color: white;
    display: flex;
    align-items: center;
}

.menu_contenedor {
    width: 95%;
    margin: 0 auto;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.menu_titulo {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
}

.menu_titulo h1 {
    user-select: none;
    cursor: pointer;
}

.barras {
    position: relative;
    width: 35px;
    height: 35px;

    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.barras div {
    position: absolute;
    width: 32px;
    height: 3px;
    background: rgb(255, 255, 255);
    border-radius: 2px;
}

.barra_a {
    transform: translateY(-10px);
}

.barra_b {
    transform: translateY(0px);
}

.barra_c {
    transform: translateY(10px);
}

.barras.cambiar div:nth-last-child(3) {
    transform: rotate(45deg);
}

.barras.cambiar div:nth-last-child(2) {

    opacity: 0;
}

.barras.cambiar div:nth-last-child(1) {
    transform: rotate(-45deg);
}

.menu_contenedor h1 {
    font-size: 1.8rem;
}

.contenedor_derecho {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
}

.buscador {
    position: relative;

    display: flex;
    align-items: center;
    justify-content: center;
}

.buscador input {
    border: none;
    padding: 8px;
    width: 420px;
    outline: none;
    border-radius: 8px;
    font-family: 'Open Sans', sans-serif;
}

.buscador svg {
    position: absolute;
    right: 0;
    cursor: pointer;
}

.anuncia {
    background: rgb(34, 199, 75);
    padding: 6px;
    border-radius: 8px;
    font-weight: 500;
    cursor: pointer;

    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
}

.botones {
    display: none;
}

.no_buscador {
    display: none;
}

.buscador_barra {
    position: absolute;
    z-index: 2;
    top: 60px;
    left: 0;
    width: 100%;
    background: rgb(35, 10, 94);
    padding: 20px 0;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
}

.buscar_contenido {
    width: 95%;
}

.buscar_contenido input {
    width: 100%;
    padding: 10px;
    border-radius: 8px;
    outline: none;
    border: none;
    font-family: 'Open Sans', sans-serif;
}

.resultado_busqueda {
    width: 98%;
    height: calc(100vh - 185px);
    padding: 0 10px;
    overflow-y: scroll;

    display: flex;
    flex-direction: column;
    gap: 25px;
}

.resultado_busqueda::-webkit-scrollbar {
    width: 8px;
    background: rgb(35, 10, 94);
    border-radius: 4px;
}

.resultado_busqueda::-webkit-scrollbar-thumb {
    background: rgb(22, 7, 58);
    border-radius: 4px;
    border-right: 1px solid rgb(35, 10, 94);
}

.resultado_contenido {
    width: 100%;
    background: rgba(0, 0, 0, 0.3);
    box-shadow: 0 5px 5px rgb(0, 0, 0, 0.5);

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.resultado_contenido .resultado_portada {
    flex-shrink: 0;
    width: 120px;
    overflow: hidden;
}

.resultado_contenido .resultado_portada img {
    width: 100%;
    transform: scale(1.05);
}

.resultado_informacion {
    padding: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 8px;
}

.resultado_informacion_datos {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 5px;
}

.resultado_informacion_datos span {
    cursor: pointer;
    font-size: 0.8rem;
    background: rgb(20, 197, 73);
    padding: 2px 6px;
    border-radius: 8px;
}

.resultado_informacion p {
    font-size: 1rem;
    display: block;
    line-height: 1.2em;
    overflow: hidden;
    height: 6.1em;
    text-overflow: ellipsis;
    word-wrap: break-word;
}

.no_lista {
    display: none;
}

.lista {
    position: absolute;
    background: rgb(35, 10, 94);
    top: 60px;
    z-index: 2;

    width: 450px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 20px;
    padding: 3% 3%;
}

.inicio, .distritos, .catalogo {
    cursor: pointer;
    font-size: 1.2rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    gap: 5px;
    fill: rgb(255, 255, 255);
}

.nombres_distritos h4 {
    cursor: pointer;
    font-weight: 400;
    margin: 10px 0 0 20px;
    padding: 2px 6px;
    font-weight: 500;
}

.nombres_distritos h4:hover {
    background: rgb(34, 199, 75);
    padding: 2px 6px;
    color: black;
    font-weight: 500;
    border-radius: 8px;
}

@media screen and (max-width: 1200px) {
    .buscador input {
        width: 300px;
    }
}

@media screen and (max-width: 768px) {

    .buscador, .anuncia {
        display: none;
    }

    .botones {
        user-select: none;

        display: flex;
        align-items: stretch;
        justify-content: center;
        gap: 5px;
    }

    .botones svg {
        cursor: pointer;
        padding: 5px;
        background: rgb(34, 199, 75);
        fill: black;
        border-radius: 5px;
    }

    .botones .boton_close {
        display: none;
    }

    .botones .boton_ver {
        display: block;
    }

    .lista {
        padding: 3% 4%;
    }
}


@media screen and (max-width: 576px) {
    .resultado_informacion {
        text-align: center;
        max-height: 185px;
    }

    .resultado_informacion h3 {
        font-size: 1rem;
    }

    .resultado_informacion p {
        font-size: 0.9rem;
        height: 6.1em;
    }

    .lista {
        width: 100%;
    }
}

@media screen and (max-width: 420px) {
    .menu_titulo h1 {
        font-size: 1.6rem;
    }

    .resultado_informacion p {
        font-size: 0.9rem;
        height: 4.9em;
    }
}